.mobile {
  width: 70%;
  transition: 0.3s ease;
}

@media screen and (max-width: 1600px) {
  .mobile {
    width: 65%;
  }
}
@media screen and (max-width: 1500px) {
  .mobile {
    width: 75%;
  }
}
@media screen and (max-width: 1300px) {
  .mobile {
    width: 85%;
  }
  .main-image-container {
    grid-column: 1/-1;
  }
  .main-content-container {
    grid-gap: 5rem;
  }
}
@media screen and (max-width: 1100px) {
  .mobile {
    width: 90%;
  }
  .hotel-page-hero img {
    height: 150%;
    margin-top: 0rem !important;
  }
  .rooms-grid-big {
    grid-column: 1/-1;
  }
  .rooms-grid-small {
    grid-column: 1/-1;
  }
  .main-content-text-hero h1 {
    font-size: 2rem;
  }
  .contacts-grid {
    grid-template-columns: 1fr !important;
    grid-gap: 5rem;
  }
  .contacts-grid-item {
    grid-column: 1/3;
    text-align: left;
  }
  .contacts-grid-item1 {
    grid-column: 1/-1;
    grid-row: auto;
    text-align: left;
  }

  .contacts-grid-item-important {
    text-align: left;
    margin-top: 4rem;
    grid-column: 1/-1;
  }
}

@media screen and (max-width: 800px) {
  .tavern-grid-item-big {
    grid-column: 1/-1;
  }
  .img-cont {
    width: 100%;
  }
  .tavern-grid-item-small {
    grid-column: 1/-1;
  }
  .mobile {
    width: 90%;
  }
  .main-content-text-hero h1 {
    font-size: 3rem;
  }
  .grid-item1-3 {
    grid-column: 1/-1;
  }
  .grid-item3-4 {
    grid-column: 1/-1;
  }
  .footer-container {
    grid-column: 1/-1;
  }
  .footer-maps {
    height: 60rem;
  }
  .footer-grid {
    grid-gap: 2rem;
  }

  .around-us-grid-item-big {
    grid-column: 1/-1;
  }
  .around-us-grid-item-small {
    grid-column: 1/-1;
  }
}
