.navigation-container {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  background: #212024;
  box-shadow: 0px 0px 15px 0px #000000;
  z-index: 100;
  height: 15vh;
}

.navigation {
  width: 80%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  list-style-type: none;
}

.navigation__logo-container {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex: 1;
  height: 8rem;
  overflow: hidden;
  width: fit-content;
  flex-direction: column;
}
.navigation__logo-container h1 {
  width: 100%;
  font-weight: 1000;
  font-size: 3rem;
  margin-block-start: 0em;
  margin-block-end: 0em;
  color: transparent;
  -webkit-text-stroke: 1px #ffeaaa;
  text-align: center;
}
.navigation__logo-container p {
  width: 100%;
  margin-top: -0.5rem;
  color: #ffeaaa9c;
  text-align: center;
  letter-spacing: 4px;
}
.navigation-item {
  flex-direction: column;
  width: fit-content;
  text-align: center;
  padding: 1.5rem 2.5rem;
  margin: 2.5rem;
  font-size: 1.2rem;
  font-weight: 300;

  display: flex;
  align-items: center;
  position: relative;
  justify-content: center;

  opacity: 1;

  color: #e2d787;
  border-top: none;
  border-left: none;
  border-right: none;
}
.navigation-item:hover {
  opacity: 0;
}
.navigation-item svg {
  font-size: 2rem;
  color: #ffeaaa;
  opacity: 1;
  margin-bottom: 0.6rem;
  overflow: overlay;
  z-index: -1;
  display: none;
}
.navigation-item:first-child {
  margin-left: 0;
  padding-left: 0;
}
.navigation-item:last-child {
  margin-right: 0;
}
