.around-us {
  display: flex;
  width: 100%;
  align-items: center;
  color: #e2d787;
  flex-direction: column;
}
.around-us-grid {
  margin-top: 5rem;
  margin-bottom: 5rem;
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: 1fr 1fr;
}
.around-us-grid-item-big {
  position: relative;
  grid-column: 1fr;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
}
.around-us-grid-item-small {
  grid-column: 1fr;
  position: relative;
}
