.hotel-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  min-height: 100vh;
}
.hotel-page h1 {
  color: #e2d787;
}
.hotel-page p {
  color: #e2d787;
}
.hotel-page-hero {
  margin-top: 12rem;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 30rem;
  overflow: hidden !important;
  background: radial-gradient(rgba(0, 0, 0, 0.329), black);
  position: relative;
}
.hotel-page-hero img {
  width: 100%;
  object-fit: cover;
  position: absolute;
  opacity: 0.5;
  margin-top: -25rem;
}
.hotel-appriciate {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.hotel-appriciate li {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex: 1;
}
.hotel-appriciate li span {
  display: flex;
  align-items: center;
  width: 100%;
  padding: 1rem;
  margin: 0.1rem;
  border: 0.5px solid rgb(238, 238, 238);
  height: stretch;
  white-space: nowrap;
}
.hotel-appriciate li span svg {
  margin-right: 1rem;
  font-size: 2rem;
  color: rgb(10, 151, 151);
}
.grid {
  margin-top: 5rem;
  margin-bottom: 5rem;
  grid-gap: 1rem;
  display: grid;
  grid-template-columns: 2fr 1fr;
}
.grid-item1-3 {
  grid-column: 1/2;
  display: flex;
  flex-direction: column;
}
.grid-item3-4 {
  flex-direction: column;
  display: flex;
  grid-column: 2/3;
  width: 100%;
}

.grid-item1-4 {
  grid-column: 1/-1;
  width: 100%;
}
.item {
  padding: 2rem 2rem;
}
