.video-background-container video {
  min-width: 100%;
  object-fit: cover;
  height: 95%;
  opacity: 1;
  position: absolute;
}
.video-background-container {
  top: 15vh;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 40vh;
  position: absolute;
}
.video-background-background {
  display: flex;
  width: 100%;
  height: 100%;
  z-index: 1;
  background: radial-gradient(rgba(0, 0, 0, 0), rgba(43, 43, 43, 0.404));
  position: absolute;
  justify-content: space-evenly;
}
.video-background-background div {
  height: 100%;
  width: 0.5px;
  background: rgba(0, 0, 0, 0.164);
}
.asasd {
  background: #ffe8a36b;
}
