.footer {
  background: #323e49;
  color: white;
  width: 100%;
  display: flex;
  align-items: flex-start;
  padding: 10rem 0rem;
  padding-bottom: 15rem;
  justify-content: center;
  position: relative;
}

.footer-grid {
  display: grid;
  grid-template-columns: 1fr 0.7fr 0.7fr;
  grid-gap: 15rem;
}
.footer-container {
  display: flex;
  flex-direction: column;
  grid-column: 1fr;
}
.footer-container h1 {
  margin-top: 2rem;
  margin-bottom: 3rem;
  font-weight: 100;
  font-size: 2.5rem;
  color: white;
  -webkit-text-stroke: 0px;
}

.footer-container p {
  font-weight: 200;
  font-size: 1.3rem;
  margin-bottom: 1.5rem;
  display: flex;
  align-items: center;
  color: rgb(236, 236, 236);
  font-family: "Montserrat Alternates", sans-serif;
}
.footer-container svg {
  font-size: 2rem;
  margin-right: 1.5rem;
}
.footer-container input {
  padding: 1.5rem;
  background: #ebe9e52f;
  margin-top: 1rem;
  margin-bottom: 2rem;
  border: 1px solid gray;
}

.footer-container textarea {
  padding: 1.5rem;
  background: #ebe9e52f;
  margin-top: 1rem;
  margin-bottom: 2rem;
}
.footer-container button {
  cursor: pointer;
  margin-top: 1rem;
  margin-bottom: 2rem;
  padding: 1rem;
  background: #aadaff;
  font-weight: 1000;
  color: #323e49;
  border: 1px solid gray;
  outline: none;
  box-shadow: 0px 0px 5px 0px #aadaff;
}
.cont-sl {
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  height: 30rem;
  width: 30rem;
}
