.contacts {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.contacts-grid {
  display: grid;
  grid-template-columns: 1fr 2fr;
  margin-top: 5rem;
  margin-bottom: 5rem;
  grid-gap: 1rem;
}

.contacts-grid-item {
  grid-column: 1fr;
  white-space: normal;
  display: flex;
  flex-direction: column;
}
.google-maps {
  width: 100%;
  height: 90%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.contacts-grid-item1 {
  text-align: right;
  grid-column: 2/3;
  grid-row: 1/6 !important;
}
.contacts-grid-item-important {
  grid-row: 6/7;
  grid-column: 2/3;
  text-align: right;
}
