.welcome-hero {
  z-index: 100;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: flex-end;
  width: 95%;
}
.welcome-hero-container {
  display: flex;
  align-items: flex-end;
  flex: 1;
  width: 90%;
  overflow: hidden;
  height: 70vh !important;
  min-height: 70vh;
  overflow: overlay;
  justify-content: center;
}
.welcome-hero h1 {
  pointer-events: none;
  font-size: 5rem;
  color: #ffecb3;
  -webkit-text-stroke: 1px #ffecb3;
  text-align: center;
  margin-block-end: 0;
  font-weight: 1000;
  letter-spacing: 2px;
  pointer-events: none;
  white-space: normal;
}
.welcome-hero span {
  color: #ffecb3;
}
.welcome-hero p {
  margin-bottom: 2rem;
  width: 100%;
  text-align: center;
  white-space: normal;
  font-size: 1.4rem;
  color: #ffecb3;
  font-weight: 200;
  padding: 1rem;
  pointer-events: none;
}
.welcome-hero__buttons button {
  margin-top: 3rem;
  background: linear-gradient(217deg, #ffa17f, #00223e);
  border: none;
  color: #ffecb3;
  font-weight: 100;
  outline: none;
  font-size: 1.1rem;
  padding: 1.3rem;
  border-radius: 50px;
  text-transform: uppercase;
  margin: 0.5rem;
  opacity: 0.9;
  border: 1px solid #ffa17f;
  font-weight: 1000;
}
.welcome-hero__buttons button:last-child {
  font-weight: 1000;
  border: 1px solid #ffa17f;

  background: transparent;
}
.welcome-hero-button h1 {
  margin-top: 1rem;
  font-size: 1rem !important;
  font-weight: 100 !important;
  -webkit-text-stroke: 0px;
}
.welcome-items {
  margin-top: 3rem;
  display: flex;
  align-items: center;
  flex: 1;
  z-index: 500;
  justify-content: space-between;
  flex-direction: column;
  width: 90%;
  padding: 10rem;
}
.welcome-item {
  width: 60%;
  margin: 0.5rem;
  display: flex;
  align-items: center;
  text-align: center;
  flex-direction: column-reverse;
  flex: 1;
  padding: 10rem;
}

.welcome-item h2 {
  font-weight: 600;
  font-size: 4rem;
  color: #ffecb3;
  letter-spacing: 5px;

  margin-block-start: 0em;
  height: 3rem;
  white-space: normal;
}
.welcome-item p1 {
  white-space: normal;
  font-size: 1.3rem;
  color: #ffecb3;
}
.welcome-item img {
  object-fit: cover;
  opacity: 1;
  box-shadow: 0px 0px 30px 0px black;
  width: 300px;
  margin: 0.5rem;
  height: 200px;
}
.welcome-item-text {
  padding: 1rem;
  flex: 1;
}
.welcome-item-text button {
  padding: 1rem 1.5rem;

  font-weight: 100;
  font-size: 1.3rem;
  border: 0.5px solid #9c8e65;
  background: linear-gradient(217deg, #ffa17f, #00223e);
  border-radius: 5px;
  color: white;
  border: 1px solid #ffa17f;
  border-radius: 50px;
  margin-left: 0;
}
.welcome-footer {
  height: 15vh;
  width: 100%;
  text-align: center;
  font-size: 1.1rem;
  font-weight: 100;
  color: #ffecb3;
  background: #212024;
  box-shadow: 0px -5px 15px 0px #000000;
  display: flex;
  align-items: center;
  justify-content: center;
}
.welcome-footer h4 {
  font-weight: 100;
}
